.wrap {
    text-align: center;
    .Abox {
        text-align: center;
        border: 5px solid #75C74E;
        width: 90%;
        padding: 100px 0px;
        margin: 0 auto;
        p {
            padding-top: 5px;
        }
        .p2 {
            color: #ABCC06;
            font-weight: bold;
            margin: 15px 0;
            .pNum1 {
                color: #fff;
                background: #AD0000;
            }
            .pNum2 {
                color: #fff;
                background: #731407;
            }
        }
        .p3 {
            color: #4c96d0;
            font-weight: bold;
            font-size: 18px;
        }
        h2 {
            font-size: 40px;
            border-bottom: 1px solid #ddd;
            padding: 5px;
        }
        button {
            margin-top: 30px;
            font-size: 20px;
            padding: 10px;
            background: #75C74E;
            color: #fff;
            border-radius: 4px;
            
        }
    }
}