.loadWrap {
    font-size: 50px;
    min-height: 300px;
    text-align: center;
    line-height: 600px;
    .loadicon {
        font-size: 100px;
        animation: loadspin infinite 5s linear;
    }
    @keyframes loadspin {
        from {transform: rotate(0deg);}
        to { transform: rotate(2turn);}
    }
}