
.Dwrap { 
    max-width:1170px;
    margin:0 auto;
    background:#000;
    color:#fff;
    text-align: center;
    
    .Dbox {
        width: 90%;
        text-align: center;
        margin: 0 auto;
        padding: 30px;
        border: 3px solid #999;
        

        .Dtitle {
            font-weight: bold;
            font-size: 20px;
            color: #999;
        }
        
        
        .Dsummary {
            font-size: 20px;
            color: #ddd;
        }
    }

}


